import React, { useRef } from "react";
import emailjs from "@emailjs/browser";
import "./email.scss";
import Facebook from "../images/facebook.png";
import Twiter from "../images/twiter.png";
import Instagram from "../images/instagram.png";

// npm i @emailjs/browser

const Email = () => {
  const form = useRef();

  const sendEmail = (e) => {
    e.preventDefault();

    emailjs
      .sendForm(
        "service_3ssbl3j",
        "template_2yu5qeu",
        form.current,
        "icnwddaTm-ishFL5F"
      )
      .then(
        (result) => {
          console.log(result.text);
          alert("message envoy!");
          window.location = "/";
        },
        (error) => {
          console.log(error.text);
        }
      );
  };

  return (
    <div id="contact-container">
      <h1 className="contact-titre">Contactez-nous</h1>
      <div className="contact-texte">
      <div>
        <p>
          Si vous avez des questions, des commentaires ou des suggestions,
          n'hsitez pas  nous contacter en utilisant le formulaire ci-dessous.
        </p>
        <p>
          Nous sommes toujours ravis d'entendre parler de nos visiteurs et de
          rpondre  leurs demandes. Nous sommes galement ouverts aux
          collaborations avec d'autres passionns de jardinage, des associations
          de protection de l'environnement et des entreprises qui partagent
          notre engagement pour la nature et le bien-tre de la plante.
        </p>
        <p>
          Si vous souhaitez travailler avec nous ou nous proposer un
          partenariat, veuillez galement utiliser le formulaire de contact
          ci-dessous et nous vous rpondrons dans les plus brefs dlais. Enfin,
          si vous souhaitez soutenir notre site et nous aider  continuer 
          partager notre passion pour les plantes, vous pouvez nous faire un don
          en utilisant le lien ci-dessous.
        </p>
        <p>
          Chaque contribution est apprcie et nous permet de continuer 
          proposer du contenu de qualit  nos visiteurs.
        </p>
        <p>Suivez-nous galement sur les rseaux</p>
        <div className="social">
          <img src={Facebook} alt="facebook icon"></img>
          <img src={Twiter} alt="Twitter icon"></img>
          <img src={Instagram} alt="Instagram icon"></img>
        </div>
  </div>
        <form ref={form} onSubmit={sendEmail}>
          <label>Name</label>
          <input type="text" name="user_name" />
          <label>Email</label>
          <input type="email" name="user_email" />
          <label>Message</label>
          <textarea name="message" />
          <input type="submit" value="Envoyer" />
        </form>
      </div>
    </div>
  );
};

export default Email;
